<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <div class="main">
      <div class="content">
        <p class="title">{{activityDetail.title}}</p>
        <p class="prompt">
          <span>提交时间：{{activityDetail.createdTime | substrTime}}</span>
        </p>
        <p v-html="activityDetail.content"></p>
      </div>
      <div class="participate">
        <span style="line-height: 45px">共{{table.total}}条回复</span>
        <div class="btn-main" style="float: right" @click="dialogVisible = true">回复</div>
      </div>
      <div class="table">
        <p v-for="item in table.list" class="reply_content">
          <span>{{item.createdTime}}</span>
          <span>{{item.content}}</span>
        </p>
      </div>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--内容回复-->
      <el-dialog title="回复"
                 :visible.sync="dialogVisible"
                 width="30%">
        <el-form :model="form" ref="form" :rules="rules">
          <el-form-item prop="content">
            <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 6}"
                      placeholder="请输入回复内容"
                      v-model="form.content" style="width: 100%">
            </el-input>
          </el-form-item>
        </el-form>
        <div style="margin-top: 20px">
          <div class="btn-submit" @click="onSubmitReply">确定</div>
          <div class="btn-clear" style="margin-left: 20px" @click="dialogVisible = false">取消</div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      let vm = this
      return {
        id: vm.$route.query.id,
        nav: [{name: '家长信箱', path: 'go(-1)'}, {name: '意见详情', path: ''}],
        activityDetail: [],
        table: {
          url: '/mailbox/queryPatriarchMailboxReplyPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        rules: {
          content: [
            {required: true, message: '请输入回复内容', trigger: 'blur'}
          ],
        },
        dialogVisible: false,
        // 回复新增
        form: {
          mailId: vm.$route.query.id,
          content: null
        }
      }
    },
    methods: {
      async onSubmitReply () {
        await this.$refs.form.validate()
        await this.$ajax({
          url: '/mailbox/addPatriarchMailboxReply.do',
          query: this.form
        })
        this.$remind('回复成功')
        this.dialogVisible = false
        this.$search(this.table)
      }
    },
    async mounted () {
      this.activityDetail = await this.$ajax({
        url: '/mailbox/getPatriarchMailbox.do',
        el: '.content',
        query: {
          id: this.id
        }
      })
      this.table.query.mailId = this.id
      this.$search(this.table)
    },
    watch: {
      dialogVisible () {
        if (!this.dialogVisible) {
          this.form.content = null
          this.$search(this.table)
        }
      }
    },
  }
</script>

