        color: $fc-def
    .participate
      margin-bottom: 20px
      padding: 0 0 10px 0
      border-bottom: 1px solid $border
      height: 45px
    .reply_content
      span
        display: block
        padding: 5px 0
    /deep/ .el-dialog
      border-radius: 10px 10px 0 0
      .el-dialog__header
